import { useColorModeValue } from '@chakra-ui/color-mode'
import { Box, Flex, Heading, HStack, Text, Grid } from '@chakra-ui/layout'
import { useMediaQuery } from '@chakra-ui/react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { startChatActivate, startLoadMessage } from '../../actions/chat'
import getAvatar from '../../helpers/avatarUrl'
import axs from '../../helpers/axios'
import { disconnectedIn } from '../../helpers/dateFormat'
import { scrollBottom } from '../../helpers/scroll'
// import getAvatar from '../../helpers/avatarUrl'
import UserAvatar from '../forms/UserAvatar'

const ChatItem = ({ chat, setShow }) => {
    const dispatch = useDispatch();
    const userSelected = useSelector(state => state.chat.userSelected)
    const active = useColorModeValue('gray.200', 'gray.700');
    const [isLg] = useMediaQuery('(min-width: 62em)')

    const handleClick = () => {

        if (userSelected?._id !== chat._id) {
            setShow(isLg)
            dispatch(startChatActivate(chat));
            axs({
                url: `message/${chat._id}`,
                headers: { 'Authorization': localStorage.getItem('token') }
            }).then((res) => {
                dispatch(startLoadMessage(res.data))
                scrollBottom('chatContainer', 0)
            }).catch((err) => {
                console.log(err.response);
            })
        }
    }
    return (

        <Flex
            justify='space-between' py={4} px={2}
            _hover={{ backgroundColor: active }}
            cursor='pointer' rounded='md'
            onClick={handleClick}
            bg={
                chat._id === userSelected?._id ? active : 'initial'
            }
        >
            <HStack >
                <UserAvatar online={chat.online} src={chat.picture ?? getAvatar(chat.email)} />
                <Grid>
                    <Heading size='sm'>{chat.username}</Heading>
                    <Text color={'gray.500'} whiteSpace='nowrap' overflow='hidden' textOverflow='ellipsis' >
                        {chat.sendMe ? null : 'T:'}{chat.content}
                    </Text>
                </Grid>
            </HStack>

            <Box whiteSpace='nowrap'>
                {chat.online ? (
                    <Text color='green' fontSize='sm'>En linea</Text>
                ) : (
                    <Text fontSize='sm'>{disconnectedIn(chat.lastLogin)}</Text>
                )}
                <Text color='green' fontSize='sm'>{chat.typing ? 'Escribiendo...' : ''}</Text>
            </Box>
        </Flex>
    )
}

export default ChatItem
