import { useColorModeValue } from '@chakra-ui/color-mode'
import { Box, Flex, Heading, Text } from '@chakra-ui/layout'
import { useMediaQuery } from '@chakra-ui/media-query'
import { Button } from '@chakra-ui/react'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import MainChat from '../components/chat/MainChat'
import SidebarChat from '../components/chat/SidebarChat'
const ChatPage = () => {
    const userSelected = useSelector(state => state.chat.userSelected)
    const [isMd] = useMediaQuery('(min-width: 48em)')
    const [show, setShow] = useState(isMd)

    const bgChat = useColorModeValue('white', 'gray.900')

    return (
        <>
            <Box position='relative' h='full' >
                <Button m='2' onClick={() => setShow(prev => !prev)} position='absolute' right='0'>{show ? 'X' : '>'}</Button>
                {show && (<SidebarChat setShow={setShow} />)}
            </Box>
            <Box bg={bgChat} flex={1} >
                {userSelected ? <MainChat /> : <ChatNoSelected />}

            </Box>
        </>
    )
}

const ChatNoSelected = () => {
    return (
        <Flex align="center" justify="center" h='full' direction='column'>
            <Heading
                textAlign='center'
                fontWeight='extrabold'
                fontSize={{ base: '2xl', sm: '3xl', md: '4xl', lg: '6xl' }}
                lineHeight={'110%'}
                color={'green.400'}
                bgClip='text'
                bgGradient='linear(to-l, #5028ca, #00ff95bf)'
            >
                No a seleccionado ningn chat <br />
            </Heading>
            <Text as={'span'} color='gray.600' fontSize='xl' textAlign='center'>
                usa la lupa para buscar personas
            </Text>
        </Flex>
    )
}

export default ChatPage
