import { Link as LinkRoute } from 'react-router-dom'
import { ChatIcon } from "@chakra-ui/icons"
import { Box, Button, Center, Flex, Heading, IconButton, Image, Link, Stack, Text, useColorModeValue } from "@chakra-ui/react"
import { useContext, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { startChatActivate, startLoadMessage } from "../../actions/chat"
import { AuthContext } from "../../contexts/AuthContext"
import getAvatar from "../../helpers/avatarUrl"
import axs from "../../helpers/axios"
import { scrollBottom } from "../../helpers/scroll"
import UserAvatar from "../forms/UserAvatar"


const UserCard = ({ user: usr }) => {
    const bgBtn = useColorModeValue('#151f21', 'gray.900')
    const [user, setUser] = useState(usr)
    const { auth: { user: me }, setAuth } = useContext(AuthContext)
    const dispatch = useDispatch()
    const userSelected = useSelector(state => state.chat.userSelected)
    const toFollow = () => {
        axs({
            url: 'user/follow',
            method: 'POST',
            data: { id: user._id },
            headers: { 'Authorization': localStorage.getItem('token') }
        }).then(res => {
            setUser(res.data.user)
            setAuth(prev => ({ ...prev, user: res.data.me }))
        }).catch(err => {
            console.log(err.response);
        })
    }
    const handleClick = () => {
        if (userSelected?._id !== user._id) {
            dispatch(startChatActivate(user));
            axs({
                url: `message/${user._id}`,
                headers: { 'Authorization': localStorage.getItem('token') }
            }).then((res) => {
                dispatch(startLoadMessage(res.data))
                scrollBottom('chatContainer', 0)
            }).catch((err) => {
                console.log(err.response);
            })
        }
    }
    return (
        <Center py={6}>
            <Box
                maxW={'270px'}
                w={'full'}
                bg={useColorModeValue('white', 'gray.800')}
                boxShadow={'2xl'}
                rounded={'md'}
                overflow={'hidden'}>
                <Image
                    h={'120px'}
                    w={'full'}
                    src='https://fondosmil.com/fondo/31355.jpg'
                    objectFit={'cover'}
                />
                <Flex justify={'center'} mt={-12}>
                    <UserAvatar src={user?.picture ?? getAvatar(user.email)} />

                </Flex>

                <Box p={6}>
                    <Stack spacing={0} align={'center'} mb={5}>
                        <Heading fontSize={'xl'} fontWeight={500} fontFamily={'body'}>
                            {user.firstName} {user.lastName}
                        </Heading>
                        <Text color={'gray.500'}>{user.email}</Text>
                    </Stack>

                    <Stack direction={'row'} justify={'center'} spacing={6}>
                        <Stack spacing={0} align={'center'}>
                            <Text fontWeight={600}>{user.followers.length}</Text>
                            <Text fontSize={'sm'} color={'gray.500'}>
                                Followers
                            </Text>
                        </Stack>
                        <Stack spacing={0} align={'center'}>
                            <Text fontWeight={600}>{user.following.length}</Text>
                            <Text fontSize={'sm'} color={'gray.500'}>
                                Following
                            </Text>
                        </Stack>
                    </Stack>
                    {
                        user.followers.some(foll => foll._id === me._id) ? (
                            <Button
                                my='2'
                                w='full'
                                variant='outline'
                                size='sm'
                                bg={bgBtn}
                                color={'white'}
                                rounded={'md'}
                                _hover={{
                                    transform: 'translateY(-2px)',
                                    boxShadow: 'lg',
                                }}
                                onClick={toFollow}>
                                UnFollow
                            </Button>
                        ) : (
                            <Button
                                my='2'
                                w='full'
                                size='sm'
                                bg={bgBtn}
                                color={'white'}
                                rounded={'md'}
                                _hover={{
                                    transform: 'translateY(-2px)',
                                    boxShadow: 'lg',
                                }}
                                onClick={toFollow}>
                                Follow
                            </Button>
                        )
                    }

                    <Link to='/messages' onClick={handleClick} as={LinkRoute} variant='solid'>
                        <IconButton
                            w='full'
                            mx='1' size='sm'
                            icon={<ChatIcon />} _hover={{
                                transform: 'translateY(-2px)',
                                boxShadow: 'lg',
                            }}
                        />
                    </Link>
                </Box>
            </Box>
        </Center>
    )
}

export default UserCard
