
import { Box, Divider, Flex, Grid, Heading, HStack, Text, } from '@chakra-ui/layout'
import { IconButton } from '@chakra-ui/react'
import UserAvatar from '../forms/UserAvatar'
import { useSelector } from 'react-redux';
import getAvatar from '../../helpers/avatarUrl';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import '../../variables/iconos'
import { dateFormat } from '../../helpers/dateFormat';
import { Menu, MenuButton, MenuItem, MenuList } from '@chakra-ui/react';
import { ExternalLinkIcon, HamburgerIcon } from '@chakra-ui/icons';
import { Link } from 'react-router-dom';

const NavbarChat = () => {
    const userSelected = useSelector(state => state.chat.userSelected)
    return (
        <Box rounded='lg'>
            <Flex alignItems={'center'} justifyContent={'space-between'} py={3} mx='1'>
                <HStack>
                    <UserAvatar src={userSelected.picture ?? getAvatar(userSelected.email)} online={userSelected.online} />
                    <Grid>
                        <Heading fontSize={{ base: 'md', lg: 'xl' }} fontFamily={'body'} whiteSpace='nowrap' overflow='hidden' textOverflow='ellipsis'>
                            {userSelected.firstName} {userSelected.lastName}
                        </Heading>
                        <Flex direction={{ base: 'column', lg: 'row' }} >
                            <Text me='3' fontWeight={600} color={'gray.500'}>
                                {userSelected.email}
                            </Text>
                            <Text fontSize='small' my='auto'>
                                <FontAwesomeIcon icon={['fa', 'clock']} />  {dateFormat(userSelected.lastLogin)}
                            </Text>
                        </Flex>
                    </Grid>
                </HStack>

                <Flex alignItems={'center'}>
                    <Menu>
                        <MenuButton
                            as={IconButton}
                            aria-label='Options'
                            icon={<HamburgerIcon />}
                            variant='outline'
                        />
                        <MenuList>
                            <Link to={`/profile/${userSelected._id}`}>
                                <MenuItem icon={<ExternalLinkIcon />} >
                                    Ver perfil
                                </MenuItem>
                            </Link>
                        </MenuList>
                    </Menu>
                </Flex>
            </Flex>
            <Divider />
        </Box>
    )
}

export default NavbarChat
