import { Button, Flex, Heading, Text } from "@chakra-ui/react"
import { Link } from "react-router-dom"

const NotFound = () => {
    return (
        <Flex py={10} px={6} align="center" w='full' justify="center" direction='column'>
            <Heading
                display="inline-block"
                size="3xl"
                bgGradient="linear(to-r, teal.400, teal.600)"
                backgroundClip="text">
                404
            </Heading>
            <Text fontSize="18px" mt={3} mb={2}>
                Not Found
            </Text>
            <Text color={'gray.500'} mb={6}>
                No encontramos la pgina que est intentando acceder
            </Text>

            <Link to='/'>
                <Button
                    colorScheme="teal"
                    bgGradient="linear(to-r, teal.400, teal.500, teal.600)"
                    color="white"
                    variant="solid">
                    Ir a inicio
                </Button></Link>
        </Flex>
    )
}

export default NotFound
