import { types } from "../variables/types";


export const startListUsers = (users) => ({
    type: types.chatListUsers,
    payload: users
})


export const startChatActivate = (user) => ({
    type: types.chatActivate,
    payload: user
})

export const startSendMessage = (message) => ({
    type: types.chatSendMessage,
    payload: message
})


export const startLoadMessage = (messages) => ({
    type: types.chatLoad,
    payload: messages
})

export const startLogout = () => ({
    type: types.authLogout
})

export const startChatsList = (chats) => ({
    type: types.chatlist, payload: chats
})

export const startChatTyping = (id) => ({
    type: types.chatTyping, payload: id
})
export const startNotChatTyping = (id) => ({
    type: types.chatNotTyping, payload: id
})