import React, { useContext } from 'react'
import { Button } from '@chakra-ui/button'
import { useColorModeValue } from '@chakra-ui/color-mode'
import { Box, Heading, Stack } from '@chakra-ui/layout'
import InputText from '../forms/InputText'
import { useFormik } from 'formik';
import InputPassword from '../forms/InputPassword'
import { Link } from 'react-router-dom'
import { loginValidator } from '../../validators/validator'
import { FormControl, FormLabel } from '@chakra-ui/form-control'
import { Switch } from '@chakra-ui/switch'
import { AuthContext } from '../../contexts/AuthContext'
import axs from '../../helpers/axios'


const Login = () => {

    const { setAuth } = useContext(AuthContext)


    const login = (username, password) => {

        axs({
            method: 'POST',
            url: 'auth/login',
            data: { username, password }
        }).then((res) => {
            console.log(res);
            const { user, token } = res.data
            localStorage.setItem('token', token)
            setAuth({ user, token, cheking: false })

        }).catch((err) => {
            console.log(err.response);
            if (err.response?.data) {
                formik.setErrors(err.response.data)
            }
        })
    }

    const handleSubmit = (data) => {
        const remember = data.remember
        remember ? localStorage.setItem('username', data.username) : localStorage.removeItem('username')
        login(data.username, data.password)
    }

    const formik = useFormik({
        initialValues:
        {
            remember: true,
            username: localStorage.getItem('username')
        },
        onSubmit: handleSubmit,
        validationSchema: loginValidator
    })


    return (
        <Box as={'form'} mt={10} onSubmit={formik.handleSubmit} >
            <Heading mb='2'>Login</Heading>
            <Stack spacing={4}>
                <InputText
                    errors={formik.errors.username}
                    touched={formik.touched.username}
                    isRequired={true}
                    label='Username'
                    help='Ingrese su nombre de usuario'
                    type='text'
                    variant="filled"
                    placeholder="Usuario"
                    _focus={{ bg: useColorModeValue("ini", "gray.600") }}
                    autoComplete="none"
                    {...formik.getFieldProps('username')}
                />

                <InputPassword
                    errors={formik.errors.password}
                    touched={formik.touched.password}
                    isRequired={true}
                    label='Password'
                    help='Ingrese la contrasea'
                    variant="filled"
                    placeholder="Contrasea"
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('password')}
                />

                <FormControl display="flex" alignItems="center">
                    <FormLabel htmlFor="email-alerts" mb="0">
                        Recordarme cuenta?
                    </FormLabel>
                    <Switch {...formik.getFieldProps('remember')} defaultChecked />
                </FormControl>

            </Stack>
            <Button
                type="submit"
                fontFamily={'heading'}
                mt={8}
                w={'full'}
                bgGradient="linear(to-r, red.400,pink.400)"
                color={'white'}
                _hover={{
                    bgGradient: 'linear(to-r, red.400,pink.400)',
                    boxShadow: 'xl',
                }}
                disabled={formik.isValid ? false : true}
            >
                Iniciar sesin
            </Button>

            <Link to="/auth/signup" >
                <Button
                    w="full"
                    variant="link"
                    shadow="none"
                    _focus={{ outline: "none" }}
                >
                    Crear cuenta
                </Button>
            </Link>
        </Box>
    )
}

export default Login
