import React from 'react';
import { FormControl, FormErrorMessage, FormHelperText, FormLabel } from '@chakra-ui/form-control'
import { Input, } from '@chakra-ui/input'
import { ListItem, UnorderedList } from '@chakra-ui/layout'

const InputText = ({ errors, touched, isRequired = false, label, help, ...rests }) => {
    const { value, ...rest } = rests
    return (
        <FormControl isInvalid={errors && touched} isRequired={isRequired}>
            <FormLabel>{label}</FormLabel>

            <Input
                {...rest}
                value={value || ''}
            />

            {errors && touched ? (
                <FormErrorMessage>
                    <UnorderedList>
                        {
                            errors instanceof Array ? (errors.map((error, key) => (<ListItem key={key}>{error}</ListItem>))) : (<ListItem>{errors}</ListItem>)
                        }
                    </UnorderedList>
                </FormErrorMessage>
            ) : (
                <FormHelperText>{help}</FormHelperText>
            )}
        </FormControl>
    )
}

export default InputText
