import { Avatar } from '@chakra-ui/avatar'
import React from 'react'

const UserAvatar = ({ size = 'md', src, online = true, ...rest }) => {
    return (
        <Avatar
            {...rest}
            size={size}
            src={src}
            alt={'avatar'}
            mx={2}
            pos={'relative'}
            border='2px solid white'
            _after={{
                content: '""',
                w: 4,
                h: 4,
                bg: online ? 'green.300' : 'red.300',
                border: '2px solid white',
                rounded: 'full',
                pos: 'absolute',
                bottom: 0,
                right: 3,
            }}
        />
    )
}

export default UserAvatar
