import { types } from "../variables/types";


const initialState = {
    userSelected: null,//id del user seleccinado
    users: [], //todo los usuarios 
    chats: [],//todo los chats del user
    messages: [],// chat seleccionado

}

export const chatReducer = (state = initialState, action) => {

    switch (action.type) {

        case types.chatListUsers:
            return { ...state, users: action.payload }

        case types.chatActivate:
            if (state.userSelected?._id === action.payload._id) {
                return state
            }
            return { ...state, userSelected: action.payload, messages: [] }

        case types.chatSendMessage:
            if (
                state.userSelected?._id === action.payload.from._id ||
                state.userSelected?._id === action.payload.to._id
            ) {
                return {
                    ...state, messages: [...state.messages, action.payload]
                }
            }
            return state
        case types.chatLoad:
            return { ...state, messages: action.payload }

        case types.authLogout:
            return initialState

        case types.chatlist:
            return { ...state, chats: action.payload }

        case types.chatTyping:
            return { ...state, chats: state.chats.map(ch => ch._id === action.payload ? { ...ch, typing: true } : ch) }
        case types.chatNotTyping:
            return { ...state, chats: state.chats.map(ch => ch._id === action.payload ? { ...ch, typing: false } : ch) }
        default: return state;
    }

}