import { ChatIcon } from "@chakra-ui/icons";
import { Box, Button, Flex, Grid, HStack, IconButton, Text, useColorModeValue } from "@chakra-ui/react"
import { useContext, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link as LinkRoute, useNavigate } from "react-router-dom";
import { startChatActivate, startLoadMessage } from "../../actions/chat";
import { AuthContext } from "../../contexts/AuthContext";
import getAvatar from "../../helpers/avatarUrl";
import axs from "../../helpers/axios";
import { scrollBottom } from "../../helpers/scroll";
import UserAvatar from "../forms/UserAvatar";
import { Link } from '@chakra-ui/react'
const UserItem = ({ user: usr }) => {
    const bgActive = useColorModeValue('gray.200', 'gray.700');
    const bgBtn = useColorModeValue('#151f21', 'gray.800')
    const { auth: { user: me }, setAuth } = useContext(AuthContext)
    const [user, setUser] = useState(usr)
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userSelected = useSelector(state => state.chat.userSelected)

    const toFollow = () => {
        axs({
            url: 'user/follow',
            method: 'POST',
            data: { id: user._id },
            headers: { 'Authorization': localStorage.getItem('token') }
        }).then(res => {
            setUser(res.data.user)
            setAuth(prev => ({ ...prev, user: res.data.me }))
        }).catch(err => {
            console.log(err.response);
        })
    }


    const viewProfile = () => {
        navigate(`/profile/${user._id}`)
    }


    const handleClick = () => {
        if (userSelected?._id !== user._id) {
            dispatch(startChatActivate(user));
            axs({
                url: `message/${user._id}`,
                headers: { 'Authorization': localStorage.getItem('token') }
            }).then((res) => {
                dispatch(startLoadMessage(res.data))
                scrollBottom('chatContainer', 0)
            }).catch((err) => {
                console.log(err.response);
            })
        }
    }
    return (
        <Flex
            justify='space-between' py={2}
            _hover={{ backgroundColor: bgActive }}
            cursor='pointer' rounded='md'
            align='center'
        >
            <HStack >
                <UserAvatar online={user.online} src={user.avatar ?? getAvatar(user.email)} />
                <Grid>
                    <Text whiteSpace='nowrap' overflow='hidden' textOverflow='ellipsis'>
                        <Link variant='link' size='sm' onClick={viewProfile} >{user.firstName + ' ' + user.lastName}</Link>
                    </Text>
                    <Text color={'gray.500'} whiteSpace='nowrap' overflow='hidden' textOverflow='ellipsis' >
                        {user.email}
                    </Text>
                </Grid>
            </HStack>

            <Box whiteSpace='nowrap' mx='2' textAlign='end'>
                {
                    user.followers.some(foll => foll._id === me._id) ? (
                        <Button
                            variant='outline'
                            size='sm'
                            bg={bgBtn}
                            color={'white'}
                            rounded={'md'}
                            _hover={{
                                transform: 'translateY(-2px)',
                                boxShadow: 'lg',
                            }}
                            onClick={toFollow}>
                            UnFollow
                        </Button>
                    ) : (
                        <Button
                            size='sm'
                            bg={bgBtn}
                            color={'white'}
                            rounded={'md'}
                            _hover={{
                                transform: 'translateY(-2px)',
                                boxShadow: 'lg',
                            }}
                            onClick={toFollow}>
                            Follow
                        </Button>
                    )
                }
                <Link to='/messages' onClick={handleClick} as={LinkRoute} variant='solid'>
                    <IconButton
                        mx='1' size='sm'
                        icon={<ChatIcon />} _hover={{
                            transform: 'translateY(-2px)',
                            boxShadow: 'lg',
                        }}
                    />
                </Link>
            </Box>
        </Flex>
    )
}

export default UserItem
