import { Avatar } from '@chakra-ui/avatar';
import { Button } from '@chakra-ui/button';
import { useColorModeValue } from '@chakra-ui/color-mode';
import { Box, Flex, Grid, GridItem, List, ListItem, Text } from '@chakra-ui/layout';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useContext, useEffect, useState } from 'react'
import getAvatar from '../helpers/avatarUrl';
import '../variables/iconos'
import { dateFormat } from '../helpers/dateFormat';
import { Table, Tbody, Td, Tfoot, Th, Thead, Tr } from '@chakra-ui/react';
import UserItem from '../components/home/UserItem';
import { Link, useParams } from 'react-router-dom';
import axs from '../helpers/axios';
import { AuthContext } from '../contexts/AuthContext';
import NotFound from '../components/others/NotFound';
import Loading from '../utils/Loading';
import { startChatActivate } from '../actions/chat';
import { useDispatch } from 'react-redux';
import { ChatIcon } from '@chakra-ui/icons';
const ProfilePage = () => {

    const [btnOption, setBtnOption] = useState(1);
    const bgProfile = useColorModeValue(
        "hsla(0,0%,100%,.8)",
        "linear-gradient(112.83deg, rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0) 110.84%)"
    );

    const bgCard = useColorModeValue('white', 'gray.800')
    const { auth: { user: me }, setAuth } = useContext(AuthContext)
    const { id } = useParams()
    const [user, setUser] = useState(!id ? me : null)
    const [loading, setLoading] = useState(false)
    const dispatch = useDispatch()

    useEffect(() => {
        if (id) {
            setLoading(true)
            axs({
                url: `user/${id}`,
                method: 'POST',
                headers: { 'Authorization': localStorage.getItem('token') }
            }).then(res => {
                setUser(res.data)
                setLoading(false)
            }).catch(err => {
                console.log(err.response);
                setLoading(false)
            })
        } else {
            setUser(me)
        }

    }, [id, me])

    if (loading) {
        return (<Loading />)
    }

    if (!user) {
        return (<NotFound />)
    }


    const toFollow = () => {
        axs({
            url: 'user/follow',
            method: 'POST',
            data: { id: user._id },
            headers: { 'Authorization': localStorage.getItem('token') }
        }).then(res => {
            console.log(res.data);
            setAuth(prev => ({ ...prev, user: res.data.me }))
            setUser(res.data.user)
        }).catch(err => {
            console.log(err.response);
        })
    }


    const openMessage = () => {
        dispatch(startChatActivate(user));
    }

    return (
        <Flex direction="column" flex='1' p='3'>
            <Box mb='5'>
                <Box
                    bgImage='https://fondosmil.com/fondo/31355.jpg'
                    w="100%"
                    h={{ base: '200px', lg: '300px' }}
                    borderRadius="2xl"
                    bgPosition="50%"
                    bgRepeat="no-repeat"
                    backgroundSize="cover"
                >

                </Box>
                <Flex
                    direction={{ base: "column", md: "row" }}
                    w='90%'
                    justifyContent={{ sm: "center", md: "space-between" }}
                    align="center"
                    backdropFilter="saturate(200%) blur(60px)"
                    boxShadow="0px 2px 5.5px rgba(0, 0, 0, 0.02)"
                    border="2px solid"
                    borderColor="blue.900"
                    bg={bgProfile}
                    p="15px"
                    rounded="2xl"
                    mt='-60px'
                    mx='auto'

                >
                    <Flex
                        align="center"
                        mb={{ sm: "10px", md: "0px" }}
                        direction={{ base: "column", md: "row" }}
                        w={{ sm: "100%" }}
                        textAlign={{ sm: "center", md: "start" }}
                    >
                        <Avatar
                            me={{ md: "22px" }}
                            src={user.picture ?? getAvatar(user.email)}
                            w="80px"
                            h="80px"
                            borderRadius="15px"
                        />
                        <Flex direction="column" maxWidth="100%" my={{ sm: "14px" }}>
                            <Text
                                fontSize={{ sm: "lg", lg: "xl" }}
                                color={"gray.200"}
                                fontWeight="bold"
                                ms={{ sm: "8px", md: "0px" }}
                            >
                                {user.firstName + ' ' + user.lastName}
                            </Text>
                            <Text
                                fontSize={{ sm: "sm", md: "md" }}
                                color={"gray.400"}
                                fontWeight="semibold"
                            >
                                {user.email}
                            </Text>

                            {id && id !== me._id && (
                                <Box>
                                    {
                                        user.followers.some(foll => foll._id === me._id) ? (
                                            <Button size='sm' variant='outline' onClick={toFollow}>unfollow</Button>
                                        ) : (
                                            <Button size='sm' variant='solid' onClick={toFollow}>follow</Button>
                                        )
                                    }
                                    <Link to='/messages' onClick={openMessage}>
                                        <Button size='sm' variant='solid' ms='1'><ChatIcon /></Button>
                                    </Link>
                                </Box>
                            )}
                        </Flex>
                    </Flex>
                    <Flex
                        direction={{ base: "column", lg: "row" }}
                        w={{ sm: "100%", md: "50%", lg: "auto" }}
                    >
                        <Button
                            bg={btnOption === 1 ? "hsla(0,0%,100%,.3)" : "transparent"}
                            borderRadius="15px"
                            _focus={{ shadow: "none" }}
                            onClick={() => {
                                setBtnOption(1);
                            }}
                            _hover={{}}
                        >
                            <FontAwesomeIcon icon={['fa', 'user']} />
                            <Text fontSize="xs" fontWeight="bold" mx='2'>
                                {user.followers.length} Seguidores
                            </Text>
                        </Button>
                        <Button
                            bg={btnOption === 2 ? "hsla(0,0%,100%,.3)" : "transparent"}
                            borderRadius="15px"
                            _focus={{ shadow: "none" }}
                            onClick={() => {
                                setBtnOption(2);
                            }}
                            _hover={{}}
                        >
                            <FontAwesomeIcon icon={['fa', 'user-check']} />
                            <Text fontSize="xs" fontWeight="bold" mx='2'>
                                {user.following.length} Seguidos
                            </Text>
                        </Button>
                        <Button
                            bg={btnOption === 3 ? "hsla(0,0%,100%,.3)" : "transparent"}
                            borderRadius="15px"
                            _focus={{ shadow: "none" }}
                            _hover={{}}
                        >
                            <FontAwesomeIcon icon={['fa', 'code']} />
                            <Text fontSize="xs" fontWeight="bold" mx='2'>
                                options
                            </Text>
                        </Button>
                    </Flex>
                </Flex>

            </Box>
            <Grid templateColumns="repeat(12,1fr)" gap='2'>
                <GridItem colSpan={[12, 12, 6, 4]}>
                    <Box boxShadow='xl' p={{ base: '2', lg: '6' }} rounded='2xl' bg={bgCard}>
                        <Box p="12px 5px" mb="12px">
                            <Text fontSize="lg" fontWeight="bold">
                                Mis datos
                            </Text>
                        </Box>
                        <Box px="5px">

                            <List spacing={3}>
                                <ListItem>
                                    <Text color="gray.500" fontWeight="bold" d='inline' >Username:</Text> &nbsp; <Text color="gray.500" d='inline'>{user.username}</Text>
                                </ListItem>
                                <ListItem>
                                    <Text color="gray.500" fontWeight="bold" d='inline' >Nombres</Text> &nbsp; <Text color="gray.500" d='inline'>{user.firstName}</Text>
                                </ListItem>
                                <ListItem>
                                    <Text color="gray.500" fontWeight="bold" d='inline' >Apellidos</Text> &nbsp; <Text color="gray.500" d='inline'>{user.lastName}</Text>
                                </ListItem>
                                <ListItem>
                                    <Text color="gray.500" fontWeight="bold" d='inline' >Email</Text> &nbsp; <Text color="gray.500" d='inline'>{user.email}</Text>
                                </ListItem>
                                <ListItem>
                                    <Text color="gray.500" fontWeight="bold" d='inline' >Gnero</Text> &nbsp; <Text color="gray.500" d='inline'>{user.gender}</Text>
                                </ListItem>
                                <ListItem>
                                    <Text color="gray.500" fontWeight="bold" d='inline' >ltima sesin:</Text> &nbsp; <Text color="gray.500" d='inline'> {dateFormat(user.lastLogin)}</Text>
                                </ListItem>

                            </List>

                        </Box>
                    </Box>
                </GridItem>

                <GridItem colSpan={[12, 12, 5, 7]} >
                    <Box boxShadow='xl'>
                        <TableUser users={btnOption === 1 ? user.followers : user.following} />
                    </Box>
                </GridItem>
            </Grid>

        </Flex>
    );
};

const TableUser = ({ users = [] }) => {

    return (
        <Table variant='simple' size='sm'>
            <Thead>
                <Tr>
                    <Th>Usuarios</Th>
                </Tr>
            </Thead>
            <Tbody>

                <Tr>
                    <Td>{users.map((user) => (
                        <UserItem user={user} key={user._id} />
                    ))}</Td>

                </Tr>
            </Tbody>
            <Tfoot>
                <Tr>
                    <Th isNumeric>Total : {users.length}</Th>
                </Tr>
            </Tfoot>
        </Table>
    )
}

export default ProfilePage
