import { useColorModeValue } from '@chakra-ui/color-mode'
import { Input } from '@chakra-ui/input'
import { Box, } from '@chakra-ui/layout'
import { useState } from 'react'
import axs from '../../helpers/axios'
import UserItem from '../home/UserItem'

const Search = () => {

    const [users, setUsers] = useState([])

    const handleChange = async (e) => {
        const keyword = e.target.value.trim();

        if (keyword) {
            try {

                const res = await axs({
                    method: 'POST',
                    url: `auth/list?keyword=${keyword}`,
                    headers: { 'Authorization': localStorage.getItem('token') }
                })

                setUsers(res.data)

            } catch (error) {
                console.log(error.response);
            }
        } else {
            setUsers([])
        }

    }

    return (
        <Box position="relative">
            <Input type='search' placeholder='Buscar...' variant='filled' onChange={handleChange} />
            <Box
                position="absolute"
                zIndex='100'
                bg={useColorModeValue('gray.100', 'blue.900')}
                w='full'
                rounded='lg'
                boxShadow='2xl'
            >
                {
                    users.map((user) => (
                        <UserItem key={user._id} user={user} />
                    ))
                }
            </Box>
        </Box>
    )
}




export default Search
