import { Image } from '@chakra-ui/image'
import React from 'react'
import { Container, Flex, Stack, Box } from "@chakra-ui/layout";
import { useLocation } from 'react-router';
import Login from '../components/auth/Login';
import Signup from '../components/auth/Signup';
import { useColorModeValue } from '@chakra-ui/color-mode';



const AuthPage = () => {
    const path = useLocation().pathname;
    return (
        <Container
            as={Flex}
            maxW={"7xl"}
            minH='100vh'
            align="center"
            justifyContent='center'
            flexDir={{ base: 'column-reverse', md: 'row' }}

        >
            <Stack flex='3' >
                <Image src="https://mdbootstrap.com/img/Photos/new-templates/bootstrap-login-form/draw2.svg" alt="Segun Adebayo" />

            </Stack>
            <Stack flex='2' m={5}>

                <Box bg={useColorModeValue('gray.50', 'gray.800')} p='10' rounded="2xl" boxShadow='lg'>

                    {path === "/auth/login" ? <Login /> : <Signup />}
                </Box>
            </Stack>
        </Container>
    )
}

export default AuthPage
