import React from 'react'
import { useColorModeValue } from '@chakra-ui/color-mode'
import { Flex, Stack, Text } from '@chakra-ui/layout'
import UserAvatar from '../forms/UserAvatar'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import '../../variables/iconos'
import { dateFormat } from '../../helpers/dateFormat'
import getAvatar from '../../helpers/avatarUrl'
const Chat = ({ me = false, message }) => {
    const bgChat = useColorModeValue(me ? 'gray.100' : 'cyan.100', me ? 'gray.800' : 'blue.900');
    return (
        <Flex maxW={{ base: '85%', lg: '80%' }} w='fit-content' align='end' ms={me ? 'auto' : '0'} my='2'>
            {!me && <UserAvatar size='sm' src={message.from.picture ?? getAvatar(message.from.email)} />}

            <Stack
                bg={bgChat}
                boxShadow={'lg'}
                p={8}
                rounded={'xl'}
                pos={'relative'}
                _after={{
                    content: `""`,
                    w: 0,
                    h: 0,
                    border: '10px solid transparent',
                    borderLeftColor: me ? 'transparent' : bgChat,
                    borderTopColor: me ? 'transparent' : bgChat,

                    borderRightColor: me ? bgChat : 'transparent',
                    borderBlockColor: me ? bgChat : 'transparent',

                    pos: 'absolute',
                    bottom: '-10px',
                    left: me ? 'initial' : '0',
                    right: me ? '0' : 'initial'
                }}>
                <Text

                    color={useColorModeValue('gray.600', 'gray.400')}
                    fontSize={'md'}>
                    {message.content}
                </Text>
                <Text fontSize='small' textAlign={me ? 'left' : 'right'}><FontAwesomeIcon icon={['fa', 'clock']} /> {dateFormat(message.createdAt)}</Text>

            </Stack>
            {me && <UserAvatar size='sm' src={message.from.picture ?? getAvatar(message.from.email)} />}

        </Flex>

    )
}

export default Chat
