import { Button } from '@chakra-ui/button'
import { useColorModeValue } from '@chakra-ui/color-mode'
import { Heading, Stack } from '@chakra-ui/layout'
import { useFormik } from 'formik'
import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { AuthContext } from '../../contexts/AuthContext'
import axs from '../../helpers/axios'
import { singupValidator } from '../../validators/validator'
import InputPassword from '../forms/InputPassword'
import InputSelect from '../forms/InputSelect'
import InputText from '../forms/InputText'

const Signup = () => {

    const { setAuth } = useContext(AuthContext)
    const handleSubmit = (data) => {
        axs({
            method: 'POST',
            url: '/auth/signup',
            data
        }).then(res => {
            console.log(res);
            const { user, token } = res.data
            localStorage.setItem('token', token)
            setAuth({ user, token })
        }).catch(err => {
            formik.setErrors(err.response.data)
        })
    }

    const formik = useFormik({
        initialValues: {},
        onSubmit: handleSubmit,
        validationSchema: singupValidator
    })

    return (
        <Stack spacing={4} as='form' onSubmit={formik.handleSubmit}>
            <Heading mb='2'>Signup</Heading>
            <InputText
                errors={formik.errors.username}
                touched={formik.touched.username}
                isRequired={true}
                label='Username'
                help='Ingrese un nombre de usuario'
                type='text'
                variant="filled"
                placeholder="Usuario"
                _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                autoComplete="none"
                {...formik.getFieldProps('username')}
            />

            <Stack direction={{ base: 'column', lg: 'row' }}>
                <InputPassword
                    errors={formik.errors.password}
                    touched={formik.touched.password}
                    isRequired={true}
                    label='Password'
                    help='Ingrese la contrasea'
                    variant="filled"
                    placeholder="Contrasea"
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('password')}
                />

                <InputPassword
                    errors={formik.errors.password2}
                    touched={formik.touched.password2}
                    isRequired={true}
                    label='Confirmar contrasea'
                    help='Confirmar Contrasea'
                    variant="filled"
                    placeholder="Contrasea"
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('password2')}
                />
            </Stack>
            <Stack direction={{ base: 'column', lg: 'row' }}>
                <InputText
                    errors={formik.errors.firstName}
                    touched={formik.touched.firstName}
                    isRequired={true}
                    label='Nombre '
                    help='Ingrese su nombre '
                    type='text'
                    variant="filled"
                    placeholder="Nombre "
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('firstName')}
                />
                <InputText
                    errors={formik.errors.lastName}
                    touched={formik.touched.lastName}
                    isRequired={true}
                    label='Apellidos '
                    help='Ingrese su apellido '
                    type='text'
                    variant="filled"
                    placeholder="apellidos "
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('lastName')}
                />


            </Stack>
            <Stack direction={{ base: 'column', lg: 'row' }}>
                <InputText
                    errors={formik.errors.email}
                    touched={formik.touched.email}
                    isRequired={true}
                    label='Email'
                    help='Ingrese su correo'
                    type='email'
                    variant="filled"
                    placeholder="Email"
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('email')}
                />

                <InputSelect
                    errors={formik.errors.gender}
                    touched={formik.touched.gender}
                    isRequired={true}
                    label='Gnero'
                    help='Ingrese su gnero'
                    placeholder="Seleccione el gnero..."
                    variant="filled"
                    bg={useColorModeValue("gray.50", "gray.700")}
                    _focus={{ bg: useColorModeValue("ini", "gray.700") }}
                    autoComplete="none"
                    {...formik.getFieldProps('gender')}
                    options={[
                        { value: 'Masculino', name: 'Masculino' },
                        { value: 'Femenino', name: 'Femenino' },
                        { value: 'Otro', name: 'Otro' },
                    ]}
                />
            </Stack>

            <Button
                fontFamily={'heading'}
                type="submit"
                mt={8}
                w={'full'}
                bgGradient="linear(to-r, red.400,pink.400)"
                color={'white'}
                _hover={{
                    bgGradient: 'linear(to-r, red.400,pink.400)',
                    boxShadow: 'xl',
                }}
                disabled={formik.isValid && Object.keys(formik.touched).length !== 0 ? false : true}
            >
                Crear cuenta
            </Button>

            <Link to="/auth/login" >
                <Button
                    w="full"
                    variant="link"
                    shadow="none"
                    _focus={{ outline: "none" }}
                >
                    Login
                </Button>
            </Link>

        </Stack>
    )
}

export default Signup
