import TopBar from "../components/home/TopBar"
import { Flex, Box } from '@chakra-ui/layout'
import SideBar from "../components/home/SideBar"
import MainPosts from "../components/home/MainPosts"
import RightBar from "../components/home/RightBar"
import { useBreakpointValue, useColorModeValue } from "@chakra-ui/react"
const HomePage = () => {
    const className = useBreakpointValue({ base: '', lg: 'scroll' })
    return (
        <Box w='full' className='scroll'>
            <Flex w='full' alignItems={'center'} justifyContent='space-around' h='50px' bg={useColorModeValue('gray.100', 'blue.900')} px='1'>
                <TopBar />
            </Flex>
            <Flex w='full' g={1} h='calc(100% - 50px)' direction={{ base: 'column', lg: 'row' }}>
                <Box p='1' ><SideBar /></Box>
                <Box flex={1} className={className}><MainPosts /></Box>
                <Box ><RightBar /></Box>
            </Flex>
        </Box >
    )
}

export default HomePage
