import { Box, Text } from '@chakra-ui/layout'
import { useMediaQuery } from '@chakra-ui/react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { startChatActivate, startLoadMessage } from '../../actions/chat'
import getAvatar from '../../helpers/avatarUrl'
import axs from '../../helpers/axios'
import { scrollBottom } from '../../helpers/scroll'
import UserAvatar from '../forms/UserAvatar'

const UsersActive = ({ user, setShow }) => {
    const userSelected = useSelector(state => state.chat.userSelected)
    const dispatch = useDispatch();
    const [isLg] = useMediaQuery('(min-width: 62em)')
    const handleClick = () => {
        setShow(isLg)
        if (userSelected?._id !== user._id) {
            dispatch(startChatActivate(user));

            axs({
                url: `message/${user._id}`,
                headers: { 'Authorization': localStorage.getItem('token') }
            }).then((res) => {
                dispatch(startLoadMessage(res.data))
                scrollBottom('chatContainer', 0)
            }).catch((err) => {
                console.log(err.response);
            })
        }
    }
    return (
        <Box cursor='pointer' onClick={handleClick}>
            <UserAvatar online={user.online} src={user.picture ?? getAvatar(user.email)} />
            <Text color='gray.500' textAlign='center' >{user.username}</Text>

        </Box>
    )
}

export default UsersActive
