
import { Box, Flex, useColorModeValue } from '@chakra-ui/react';
import React, { useContext, useEffect } from 'react'
import {
    Routes,
    Route,
    Navigate,
    BrowserRouter,
    Outlet,
} from "react-router-dom";
import SidebarMenu from '../components/chat/SidebarMenu';

import { AuthContext } from '../contexts/AuthContext';
import AuthPage from '../pages/AuthPage';
import ChatPage from '../pages/ChatPage';
import HomePage from '../pages/HomePage';
import ProfilePage from '../pages/ProfilePage';
import Loading from '../utils/Loading';




const AppRouter = () => {
    const { auth, checkToken } = useContext(AuthContext)


    useEffect(() => {
        checkToken();
    }, [checkToken])

    if (auth.cheking) {
        return <Loading />
    }
    return (

        <BrowserRouter >
            <Routes>
                <Route path="/*" element={<Private><HomeLayout /></Private>} >
                    <Route index element={<HomePage />} />
                    <Route path="profile" element={<ProfilePage />} />
                    <Route path="profile/:id" element={<ProfilePage />} />
                    <Route path='messages' element={<ChatPage />} />
                </Route>
                <Route path="auth/*" element={<Public> <Outlet /></Public>} >
                    <Route path="login" element={<AuthPage />} />
                    <Route path="signup" element={<AuthPage />} />
                </Route>
            </Routes>
        </BrowserRouter>
    )
}

const HomeLayout = () => {
    const bgMenu = useColorModeValue('gray.100', 'gray.700')
    return (

        <Flex h='100vh' gap='2'>
            <Box bg={bgMenu} p={1} >
                <SidebarMenu />
            </Box>

            <Outlet />
        </Flex>
    )
}


const Private = ({ children }) => {
    const { auth } = useContext(AuthContext)
    return !auth.token ? <Navigate to="auth/login" /> : children;
}

const Public = ({ children }) => {
    const { auth } = useContext(AuthContext)
    return auth.token ? <Navigate to="/" /> : children;
}



export default AppRouter
