import React, { useContext } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import '../../variables/iconos'
import { Flex, VStack, Box, Text, Stack, Heading } from '@chakra-ui/layout'
import { IconButton } from '@chakra-ui/react'
import { Button } from '@chakra-ui/button'
import { useColorMode } from '@chakra-ui/color-mode'
import { Menu, MenuButton, MenuDivider, MenuItem, MenuList } from '@chakra-ui/menu'
import { AuthContext } from '../../contexts/AuthContext';
import UserAvatar from '../forms/UserAvatar'
import getAvatar from '../../helpers/avatarUrl'
import { useDispatch } from 'react-redux'
import { startLogout } from '../../actions/chat'
import { Link } from 'react-router-dom'
import { Tooltip } from '@chakra-ui/tooltip'
import { Icon } from '@chakra-ui/react'




const SidebarMenu = () => {
    const { auth, setAuth } = useContext(AuthContext)
    const { colorMode, toggleColorMode } = useColorMode();
    const dispatch = useDispatch()
    const handleLogout = () => {
        dispatch(startLogout())
        localStorage.removeItem('token')
        setAuth({ user: null, token: null, cheking: false });
    }

    return (
        <Flex direction="column" align="center" justify='space-around' h='full'>
            <VStack>
                <Tooltip label="Home" placement='auto-start'>
                    <Link to='/'>
                        <Button >
                            <FontAwesomeIcon icon={['fa', 'home']} />
                        </Button>
                    </Link>
                </Tooltip>
                <Tooltip label="Mensajes" placement='auto-start'>
                    <Link to='/messages'>
                        <Button >
                            <FontAwesomeIcon icon={['fa', 'comment-dots']} />
                        </Button>
                    </Link>
                </Tooltip>
                <Tooltip label="Perfil" placement='auto-start'>
                    <Link to='profile/' >
                        <Button>
                            <FontAwesomeIcon icon={['fa', 'user']} />
                        </Button>
                    </Link>
                </Tooltip>
            </VStack>
            <VStack>
                {/* <Button>
                    <FontAwesomeIcon icon={['fa', 'code']} />
                </Button>
                <Button>
                    <FontAwesomeIcon icon={['fa', 'code']} />
                </Button> */}
            </VStack>
            <VStack>

                <Button onClick={toggleColorMode}>
                    {colorMode === "light" ? <FontAwesomeIcon icon={['fa', 'moon']} /> : <FontAwesomeIcon icon={['fa', 'lightbulb']} />}
                </Button>


                <Menu>
                    <MenuButton as={IconButton} icon={<UserAvatar size='sm' src={auth.user.picture ?? getAvatar(auth.user.email)} />} />
                    <MenuList zIndex='1000' border='0'  >
                        <Box p='3'>
                            <Box textAlign='center'>
                                <UserAvatar size='2xl' src={auth.user.picture ?? getAvatar(auth.user.email)} />
                            </Box>
                            <Stack spacing={0} align={'center'} mb={5}>
                                <Heading fontSize={'2xl'} fontWeight={500} fontFamily={'body'}>
                                    {auth.user.username}
                                </Heading>
                                <Text color={'gray.500'}>{auth.user.email}</Text>
                            </Stack>
                        </Box>
                        <Link to='profile/' >
                            <MenuItem command="T"> perfil </MenuItem>
                        </Link>
                        <MenuItem command="T">
                             Configuracion
                        </MenuItem>
                        <MenuDivider />
                        <MenuItem command="T" onClick={handleLogout}>
                            <Icon viewBox='0 0 200 200' color='red.500'>
                                <path
                                    fill='currentColor'
                                    d='M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0'
                                />
                            </Icon>
                            Salir
                        </MenuItem>
                    </MenuList>
                </Menu>
            </VStack>
        </Flex>
    )
}

export default SidebarMenu
