import { Box, Button, chakra, Flex, Grid, Heading, HStack, Icon, Image, Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader, ModalOverlay, Text, useColorModeValue, useDisclosure } from "@chakra-ui/react"
import { useContext, useEffect, useState } from "react"
import { AuthContext } from "../../contexts/AuthContext"
import getAvatar from "../../helpers/avatarUrl"
import axs from "../../helpers/axios"
import { dateFormat } from "../../helpers/dateFormat"
import UserAvatar from "../forms/UserAvatar"
import NewPost from "./NewPost"

const MainPosts = () => {
    const [posts, setPosts] = useState([])
    useEffect(() => {
        axs(
            {
                url: 'post',
                method: 'GET',
                headers: { 'Authorization': localStorage.getItem('token') }
            }).then((res) => {
                setPosts(res.data)
            }).catch((err => {
                console.log(err.response);
            }))
    }, [])

    return (
        <Box p='4'>
            <NewPost setPosts={setPosts} />
            {posts.map((post) => (
                <Post key={post._id} post={post} />
            ))}
        </Box>
    )
}


const Post = ({ post: pst }) => {
    const { auth: { user } } = useContext(AuthContext)
    const [post, setPost] = useState(pst)
    const handleLike = () => {
        axs({
            url: 'post/like',
            method: 'POST',
            data: { id: post._id },
            headers: { 'Authorization': localStorage.getItem('token') }
        }).then((res) => {
            setPost(res.data)
        }).catch((err) => {
            console.log(err.response);
        })
    }

    return (
        <Box bg={useColorModeValue('gray.100', 'gray.700')} rounded='xl' my='3'>
            <Box p='1'>
                <HStack justify='space-between'>
                    <HStack>
                        <UserAvatar online={post.user.online} src={post.user.avatar ?? getAvatar(post.user.email)} />
                        <Grid>
                            <Heading size='sm'>{post.user.firstName + ' ' + post.user.lastName}</Heading>
                            <Text color={'gray.500'} whiteSpace='nowrap' overflow='hidden' textOverflow='ellipsis' >
                                {post.user.email}
                            </Text>
                        </Grid>
                    </HStack>
                    <Text>{dateFormat(post.createdAt)}</Text>
                </HStack>

                <chakra.p
                    p={2}>
                    {post.content}
                </chakra.p>
            </Box>

            <Box position='relative'>
                {post.picture && (
                    <Image
                        alt={'Login Image'}
                        objectFit={'cover'}
                        src={post.picture}
                        w='full'
                        onDoubleClick={handleLike}
                    />
                )}
                <HStack position="absolute" bottom='0' m='2'>
                    <Icon viewBox="0 0 40 35" boxSize={10} color={post.likes.some(u => u._id === user._id) ? 'purple.600' : 'purple.200'} _hover={{ color: 'purple.500' }} onClick={handleLike} >
                        <path
                            fill={'currentColor'}
                            d="M10.7964 5.04553e-07C8.66112 -0.000123335 6.57374 0.632971 4.79827 1.81922C3.0228 3.00547 1.63898 4.69158 0.82182 6.66433C0.00466116 8.63708 -0.209132 10.8079 0.207477 12.9021C0.624087 14.9964 1.65239 16.9201 3.16233 18.4299L19.1153 34.3828C19.2395 34.5074 19.3871 34.6062 19.5496 34.6736C19.7121 34.741 19.8863 34.7757 20.0622 34.7757C20.2381 34.7757 20.4123 34.741 20.5748 34.6736C20.7373 34.6062 20.8848 34.5074 21.0091 34.3828L36.962 18.4272C38.9319 16.3917 40.0228 13.6636 39.9996 10.8311C39.9764 7.99858 38.8409 5.28867 36.838 3.28573C34.835 1.28279 32.1251 0.147283 29.2926 0.124081C26.4601 0.100879 23.732 1.19184 21.6965 3.1617L20.0622 4.79337L18.4305 3.1617C17.4276 2.15892 16.237 1.36356 14.9267 0.821064C13.6163 0.278568 12.2119 -0.000433066 10.7937 5.04553e-07H10.7964Z"
                        />
                    </Icon>
                    <LikesModal likes={post.likes} />
                </HStack>
            </Box>
        </Box>
    )
}



const LikesModal = ({ likes = [] }) => {
    const bgActive = useColorModeValue('gray.200', 'gray.700');
    const { isOpen, onOpen, onClose } = useDisclosure()
    return (
        <>
            <Button onClick={onOpen} variant="link">{likes.length}</Button>

            <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>likes ({likes.length})</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody>
                        {
                            likes.map((user) => (
                                <Flex
                                    justify='space-between' py={4} px={2}
                                    _hover={{ backgroundColor: bgActive }}
                                    cursor='pointer' rounded='md'
                                    key={user._id}
                                >
                                    <HStack >
                                        <UserAvatar online={user.online} src={user.avatar ?? getAvatar(user.email)} />
                                        <Grid>
                                            <Heading size='sm'>{user.firstName + ' ' + user.lastName}</Heading>
                                            <Text color={'gray.500'} whiteSpace='nowrap' overflow='hidden' textOverflow='ellipsis' >
                                                {user.email}
                                            </Text>
                                        </Grid>
                                    </HStack>

                                    <Box whiteSpace='nowrap'>
                                        <Icon viewBox="0 0 40 35" boxSize={7} color={'purple.600'}>
                                            <path
                                                fill={'currentColor'}
                                                d="M10.7964 5.04553e-07C8.66112 -0.000123335 6.57374 0.632971 4.79827 1.81922C3.0228 3.00547 1.63898 4.69158 0.82182 6.66433C0.00466116 8.63708 -0.209132 10.8079 0.207477 12.9021C0.624087 14.9964 1.65239 16.9201 3.16233 18.4299L19.1153 34.3828C19.2395 34.5074 19.3871 34.6062 19.5496 34.6736C19.7121 34.741 19.8863 34.7757 20.0622 34.7757C20.2381 34.7757 20.4123 34.741 20.5748 34.6736C20.7373 34.6062 20.8848 34.5074 21.0091 34.3828L36.962 18.4272C38.9319 16.3917 40.0228 13.6636 39.9996 10.8311C39.9764 7.99858 38.8409 5.28867 36.838 3.28573C34.835 1.28279 32.1251 0.147283 29.2926 0.124081C26.4601 0.100879 23.732 1.19184 21.6965 3.1617L20.0622 4.79337L18.4305 3.1617C17.4276 2.15892 16.237 1.36356 14.9267 0.821064C13.6163 0.278568 12.2119 -0.000433066 10.7937 5.04553e-07H10.7964Z"
                                            />
                                        </Icon>
                                    </Box>
                                </Flex>
                            ))
                        }
                    </ModalBody>

                </ModalContent>
            </Modal>
        </>
    )
}


export default MainPosts
