import { useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import io from 'socket.io-client';
import { startChatsList, startChatTyping, startListUsers, startNotChatTyping, startSendMessage } from '../actions/chat';
import { scrollBottom } from '../helpers/scroll';


export const useSocket = (serverPath) => {

    // const socket = useMemo(() => io.connect(serverPath, { transports: ['websocket'] }), [serverPath]);

    const [socket, setSocket] = useState(null)
    const [online, setOnline] = useState(false);
    const dispatch = useDispatch()

    const connectSocket = useCallback((token) => {
        const socketTem = io.connect(serverPath,
            {
                transports: ['websocket'],
                forceNew: true,
                query: { token }
            }
        )
        setSocket(socketTem)
    }, [serverPath])

    const disconnectSocket = useCallback(() => {
        socket?.disconnect();
    }, [socket])


    useEffect(() => {
        setOnline(socket?.connected);
    }, [socket])

    useEffect(() => {
        socket?.on('connect', () => setOnline(true));
    }, [socket])

    useEffect(() => {
        socket?.on('disconnect', () => setOnline(false));
    }, [socket])

    useEffect(() => {
        // obtener todo los usuarios
        socket?.on('list-users', (users) => {
            dispatch(startListUsers(users))
        })
    }, [socket, dispatch])
    useEffect(() => {
        // obtener todo los mensajes del usuario
        socket?.on('list-messages', (messages) => {
            dispatch(startChatsList(messages))
        })
    }, [socket, dispatch])

    useEffect(() => {
        // recibir mensajes privador
        socket?.on('receive-message', (message) => {
            dispatch(startSendMessage(message))
            scrollBottom('chatContainer', 0)
        })
    }, [socket, dispatch])


    useEffect(() => {
        socket?.on('typing', (id) => {
            dispatch(startChatTyping(id))
        })
    }, [socket, dispatch])

    useEffect(() => {
        socket?.on('not-typing', (id) => {
            dispatch(startNotChatTyping(id))
        })
    }, [socket, dispatch])

    return {
        socket,
        online,
        connectSocket,
        disconnectSocket,
    }
}