import { Box, Button, CloseButton, Flex, HStack, IconButton, Image, Input, Menu, MenuButton, MenuItemOption, MenuList, MenuOptionGroup, Textarea, useColorModeValue } from "@chakra-ui/react"
import { AddIcon, WarningIcon } from '@chakra-ui/icons'
import { useContext, useRef, useState } from "react"
import axs from '../../helpers/axios'
import { AuthContext } from "../../contexts/AuthContext"
import UserAvatar from "../forms/UserAvatar"
import getAvatar from "../../helpers/avatarUrl"

const NewPost = ({ setPosts }) => {
    const [file, setFile] = useState(null)
    const [content, setContent] = useState('')
    const [type, setType] = useState('public')
    const fileRef = useRef()
    const { auth: { user } } = useContext(AuthContext)
    const toPost = () => {
        if (file || content) {
            const data = new FormData();
            content && data.append('content', content)
            file && data.append('picture', file)
            data.append('type', type)

            axs({
                url: 'post',
                method: 'POST',
                headers: { 'Authorization': localStorage.getItem('token') },
                data,
            }).then((res) => {
                setPosts(prev => ([res.data, ...prev]))
                setFile(null)
                setContent('')
            }).catch((err) => {
                console.log(err.response);
            })
        }
    }
    return (
        <Box bg={useColorModeValue('gray.100', 'gray.700')} rounded='xl'>
            <HStack position='relative' >
                <Textarea placeholder="Qu estas pensando?" border='0' px='20' value={content} onChange={e => setContent(e.target.value)} />
                <Box position='absolute' >
                    <UserAvatar online={user.online} src={user.avatar ?? getAvatar(user.email)} />
                </Box>
                <Input type='file' onChange={e => setFile(e.target.files[0])} ref={fileRef} display='none' accept=".png,.jpeg,.jpg" />
            </HStack>
            <hr />{file && (
                <Box position='relative'>
                    <CloseButton bg='gray' size='lg' position='absolute' right='0' onClick={() => setFile(null)} />
                    <Image
                        alt={'No imagen'}
                        objectFit={'cover'}
                        src={URL.createObjectURL(file)}
                        w='full'
                    />
                </Box>
            )}

            <Flex p='2' justify="space-between">
                <HStack spacing='5'>
                    <Button leftIcon={<AddIcon />} variant='link' onClick={() => fileRef.current.click()}>
                        Subir foto
                    </Button>

                    <Menu closeOnSelect={false}>
                        <MenuButton as={IconButton} icon={<WarningIcon />} variant='link'> opciones
                        </MenuButton>
                        <MenuList >
                            <MenuOptionGroup defaultValue={type} title='Tipo de publicacin' type='radio' onChange={(e) => setType(e)}>
                                <MenuItemOption value='private'>Privado</MenuItemOption>
                                <MenuItemOption value='follow'>solo amigos</MenuItemOption>
                                <MenuItemOption value='public'>Pblico</MenuItemOption>
                            </MenuOptionGroup>
                        </MenuList>
                    </Menu>
                </HStack>
                <Button onClick={toPost}>Publicar</Button>
            </Flex>
        </Box>
    )
}

export default NewPost
