import { FormControl, FormErrorMessage, FormHelperText, FormLabel } from '@chakra-ui/form-control'
import { Input, InputGroup, InputRightElement } from '@chakra-ui/input'
import { ListItem, UnorderedList } from '@chakra-ui/layout'
import { Switch } from '@chakra-ui/switch'
import React, { useState } from 'react'

const InputPassword = ({ errors, touched, isRequired = false, label, help, ...rests }) => {
  const [show, setShow] = useState(false)
  const { value, ...rest } = rests
  return (
    <FormControl isInvalid={errors && touched} isRequired={isRequired}>
      <FormLabel>{label}</FormLabel>
      <InputGroup>

        <Input
          type={show ? "text" : "password"}
          value={value || ''}
          {...rest}
        />

        <InputRightElement>
          <Switch
            size="sm"
            checked={show}
            onChange={(e) => setShow(e.target.checked)}
          />
        </InputRightElement>

      </InputGroup>

      {errors && touched ? (
        <FormErrorMessage>
          <UnorderedList>
            {
              errors instanceof Array ? (errors.map((error, key) => (<ListItem key={key}>{error}</ListItem>))) : (<ListItem>{errors}</ListItem>)
            }
          </UnorderedList>
        </FormErrorMessage>
      ) : (
        <FormHelperText>{help}</FormHelperText>
      )}
    </FormControl>
  )
}

export default InputPassword
