import { useColorModeValue } from '@chakra-ui/color-mode'
import { Box, Divider, Flex, Grid, Heading } from '@chakra-ui/layout'
import React, { useContext } from 'react'
import ChatItem from './ChatItem'
import UsersActive from './UsersActive'
import { useSelector } from "react-redux";
import { useBreakpointValue } from '@chakra-ui/react'
import { AuthContext } from '../../contexts/AuthContext'
import Search from './Search'

const SidebarChat = ({ setShow }) => {
    const { auth } = useContext(AuthContext)
    const users = useSelector(state => state.chat.users)
    const chats = useSelector(state => state.chat.chats)
    const bgSidebar = useColorModeValue('gray.50', 'gray.800')
    const maxW = useBreakpointValue({ base: 'initial', lg: '500px' })
    return (
        <Flex
            direction='column'
            h='full'
            position={{ base: 'absolute', md: 'initial' }}
            zIndex='600'
            maxW={maxW}
            p={3}
            bg={bgSidebar}
        >
            <Box >
                <Heading my={2} size='lg'>Chats</Heading>
                <Search />
                <Divider my='1' />
            </Box>

            <Grid templateColumns='repeat(5,1fr)'>

                {
                    users.filter(user => auth.user._id !== user._id).map(user => (
                        <UsersActive user={user} key={user._id} setShow={setShow} />
                    ))
                }


            </Grid>

            <Heading size='md' mb={4}>Recientes</Heading>
            <Box flex='1' className='scroll' >
                {
                    chats.map(chat => (

                        <ChatItem chat={chat} key={chat._id} setShow={setShow} />
                    ))
                }

            </Box>
        </Flex>
    )
}

export default SidebarChat
