import * as yup from 'yup';

export const loginValidator = yup.object().shape({
    username: yup.string().required('El username es obligatorio'),
    password: yup.string().required('El password es obligatorio'),
})


export const singupValidator = yup.object().shape({
    username: yup.string().required('El username es obligatorio'),
    password: yup.string().required('El password es obligatorio').min(6, 'La contrasea debe contener al menos 6 caracteres'),
    password2: yup.string().required('El password es obligatorio').min(6, 'La contrasea debe contener al menos 6 caracteres').oneOf([yup.ref('password'), null], 'Las contraseas no coinciden'),
    firstName: yup.string().required('El nombre es requerido'),
    lastName: yup.string().required('El Apellido es requerido'),
    email: yup.string().required('El email es requerido').email('No es un email vlido'),
    gender: yup.string().required('El Genero es requerido'),

})