import { Button } from '@chakra-ui/button'
import { Input } from '@chakra-ui/input'
import { Flex, Stack } from '@chakra-ui/layout'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { useContext, useState } from 'react'
import { useSelector } from 'react-redux'
import { AuthContext } from '../../contexts/AuthContext'
import { SocketContext } from '../../contexts/SocketContext'
import '../../variables/iconos'

const ControlChat = () => {

    const [message, setmessage] = useState('')
    const { socket } = useContext(SocketContext)
    const { auth } = useContext(AuthContext)
    const userSelected = useSelector(state => state.chat.userSelected)

    const handleSubmit = (e) => {
        e.preventDefault()
        if (message.trim().length !== 0) {
            // enviar mensaje
            socket.emit('send-message', {
                from: auth.user._id,
                to: userSelected._id,
                content: message
            })
            setmessage('')
        }
    }

    const emitTyping = () => {
        console.log('typing');
        socket.emit('typing', { from: auth.user._id, to: userSelected._id })

    }
    const emitNotTyping = () => {
        console.log('no typing');
        socket.emit('not-typing', { from: auth.user._id, to: userSelected._id })
    }
    return (
        <Flex
            h={16}
            alignItems={'center'}
            justifyContent={'space-between'}
            p={4}
            as='form'
            onSubmit={handleSubmit}
        >

            <Input type='text' value={message} placeholder='Enviar mensaje' variant='filled'
                onChange={(e) => {
                    setmessage(e.target.value);
                }}
                onFocus={emitTyping}
                onBlur={emitNotTyping}
            />

            <Stack direction={'row'} spacing={7} mx='3'>

                <Button type='submit' ><FontAwesomeIcon icon={['fa', 'paper-plane']} /></Button>
            </Stack>

        </Flex>
    )
}

export default ControlChat
